.wrapBanner {
    margin: 40px 0 80px 0;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    gap: 16px;
    width: 100%;
    box-sizing: border-box;
}

.wrapItem {
    border: 1.15px solid #f1f1f1;
    border-radius: 27px;
    padding: 14px 18px;
    width: 100%;
    /* height: 126px;  */
    box-shadow: 0 -1px 14px 0 rgba(29, 30, 33, 0.02);
background: #fdfdfd; 
display: flex;
flex-direction: column;
gap: 14px;
box-sizing: border-box;
}

.wrapTitle {
    display: flex;
    align-items: center;
    gap: 8px;
}
.wrapNumber{
    font-size: 24px;
    line-height: 100%;
    text-align: center;
    color: #59b17a; 
    border-radius: 100%;
background-color: rgba(231, 241, 237, 0.4);
width: 54px;
height: 54px;
display: flex;
align-items: center;
justify-content: center;
}

.title {
font-weight: 500;
font-size: 16px;
line-height: 140%;
color: #252539;
margin: 0;
}

.pct {
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #252539; 
}
.btn {
font-size: 13px;
line-height: 140%;
color: #93939a;
border: none;
background: transparent;
}
.btn:hover {
    color: #252539;  
}